import { useEffect } from 'react';
import '../styles/signup.scss';

function Signup(props) {
    //   .
    useEffect(()=>{
        let item = "male";
        props.setGender(item);
    }, [])
    return <div className="signup">
        <h1></h1>
        <div className="password-comment">
        <p> 6 ~ 20 ,</p>
        <p>, ,   2   .</p>
        </div>
        <section id="signup-form">
            <input onChange={props.joinOnChange} name="name" type="text" id="name" placeholder="" required/>
            <input onChange={props.joinOnChange} name="id" type="email" id="email" placeholder="" required/>
            <input onChange={props.joinOnChange} name="password" type="password" id="password" placeholder="" required/>
            <input onChange={props.joinPsOnChange} type="password" name="psCheck" placeholder=" " required/>
            <div className="gender">
                <button
                    style={{ background: props.selectGender ? "white" : "#ACC6FF",
                                color: props.selectGender ? "#808080" : "white" }}
                    className="male-btn"
                    type="button"
                    onClick={()=>{
                       props.setSelectGender(!props.selectGender);
                       props.genderSelectFn(props.selectGender);
                   }}></button>
                <button 
                    style={{ background: props.selectGender ? "#ACC6FF" : "white",
                                color: props.selectGender ? "white" : "#808080" }}
                    className="female-btn"
                    type="button"
                    onClick={()=>{
                        props.setSelectGender(!props.selectGender);
                        props.genderSelectFn(props.selectGender);
                    }}
                ></button>
            </div>
            <button onClick={()=>{
                props.signupFn();
            }} className="signup-btn" type="submit"></button>
            <button className="cancel-btn" onClick={()=>{
                props.history.goBack();
            }} type="button"></button>
        </section>
    </div>
}

export default Signup;