import '../styles/login.scss';

function Login(props) {
    return <div className="login">
        <h1></h1>
        <section id="login-form" action="#">
            <input 
            onChange={props.accountOnChange} 
            name="loginId" 
            className="email" 
            type="email" 
            placeholder="" 
            />
            <input 
            onChange={props.accountOnChange} 
            name="loginPs" 
            className="password" 
            type="password" 
            placeholder="" 
            />
            <button onClick={()=>{
                props.loginFn();
            }} className="login-btn" type="submit"></button>
            <button onClick={()=>{
                props.history.push('/signup')
            }} className="signup-btn" type="button"></button>
        </section>
    </div>
}

export default Login;